  constructor(private http: HttpClient) { }


  obtener(url: string) {
    console.log("MUeree obtener");
    this.getChatss(url)
      .subscribe(respuesta => {
        console.log("subscirbe ", respuesta);
        this.chat = respuesta;
      })
  }
  
  getlstMsjsEmpAdmin(id:number) {
    console.log("Holaaaa mundo");
    this.obtener(IPRESIDENTES + "consultas/mensajes/residente/" + id.toString() );
    console.log("Muereee despues de obtener");
  } // end getConjuntos
  getlstMsjAptoAdmin(id:number) {
    this.obtener(IPRESIDENTES + "consultas/mensajes/empleado/" + id.toString() );
  }
  getlstMsjAptoEmp(id:number) {
    this.obtener(IPRESIDENTES + "consultas/mensajes/admin/" + id.toString());
  }

  getChat(){
    return this.chat;
  }//BARRETO

  public getChatss(url: string) {
    return this.http.get(url);
  }
  
  setChatActivo(nombre:string ,rol: string,id:number ){
    window.localStorage['chatnombre'] = nombre;
    window.localStorage['chatrol'] = rol;
    window.localStorage['chatid'] = id;
  }
  

  nombre: any;

  getChat1(){
    return this.chatsList;
  }

  getChat2(){
    return this.chatsList2;
  }
  getChat3(){
    return this.chatsList3;
  }
  
  getChatN(){
    this.nombre = (window.localStorage['chatnombre'] || (-1).toString);
    if(this.nombre == '-1')
      return null;
    else
      return (this.nombre);
  }

  rol: any;

  getRol(){
    this.rol = (window.localStorage['chatrol'] || (-1).toString);
    if(this.rol == '-1')
      return null;
    else
      return (this.rol);
  }

  id: any;
  getChatId(){
    this.id = Number(window.localStorage['chatid'] || -1);
    if(this.id == -1)
      return null;
    else
      return (this.id);
  }
  
